import { GameEvent } from "../data/EventName";
import { GoodType } from "../data/InterfaceCom";
import { ENUM_AUDIO_CLIP } from "../Enum";
import { Languages } from "../Languages";
import AudioManager from "../manager/AudioManager";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class BaseItem extends cc.Component {

    goods: GoodType = null;
    price: string = "0.001"
    goods_id: number = 1;

    /**保存当前所有的注册的节点点击事件 */
    allNodeClickEvent: any = {};
    allLabels: Array<any> = [];
    allTrendsLabels: Array<any> = [];
    protected onLoad(): void {
        this.allNodeClickEvent = [];
        this.allLabels = [];
        this.allTrendsLabels = [];
        this.node.walk((node) => {
            if (node.getComponent(cc.Label)) {
                this.allLabels.push(node.getComponent(cc.Label))
            }
        }, null)
    }

    start() {
        this.updateLanguage();
    }
    init() {

    }
    setData(data: any) {
        //console.log(data,"setData");
        this.goods_id = data.id;
        this.price = data.amount;

    }

    protected onEnable(): void {
        EventManager.instance.on(EventType.UPDATE_LANGUAGE, this.updateLanguage, this);
    }
    protected onDisable(): void {
        EventManager.instance.off(EventType.UPDATE_LANGUAGE, this.updateLanguage, this);
    }
    updateLanguage() {
        for (let i = 0; i < this.allLabels.length; i++) {
            let name = this.allLabels[i].node.name;
            if (Languages[name]) {
                this.allLabels[i].string = Languages[name][DataManager.instance.language]
            }
        }
        for (let i = 0; i < this.allTrendsLabels.length; i++) {
            if (this.allTrendsLabels[i]["languageKey"]) {
                if (Languages[this.allTrendsLabels[i]["languageKey"]]) {
                    this.allTrendsLabels[i].getComponent(cc.Label).string = Languages[this.allTrendsLabels[i]["languageKey"]][DataManager.instance.language]
                }
            }
        }
    }
    protected addTrendsLabel(node: cc.Node) {
        this.allTrendsLabels.push(node);
    }

    onTouch(node: cc.Node, cb: any, target?: any, scale: number = 0.9) {
        if (this.allNodeClickEvent[node.uuid]) {
            console.log("重复注册事件---------------------------")
            return;
        }
        if (!node.getComponent(cc.Button)) {
            node.addComponent(cc.Button);
            node.getComponent(cc.Button).transition = cc.Button.Transition.SCALE;
            node.getComponent(cc.Button).duration = 0.1;
            node.getComponent(cc.Button).zoomScale = scale;
        }
        node.getComponent(cc.Button).interactable = true;
        node.getComponent(cc.Button).enableAutoGrayEffect = false;
        if (!node["isClick"]) {
            node.attr({ isClick: 1 })
        }
        let callback = (t) => {
            if (node["isClick"] == 2) {
                return;
            }
            AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
            cb && cb.call(target)
            node["isClick"] = 2;
            this.scheduleOnce(() => {
                node["isClick"] = 1;
            }, 0.3)
        }
        node.on('click', callback, this);
        this.allNodeClickEvent[node.uuid] = {
            node: node,
            callback: callback,
            target: this
        }
    }
    protected onDestroy(): void {
        for (let key in this.allNodeClickEvent) {
            let item = this.allNodeClickEvent[key];
            item.node.off('click', item.callback, item.target);
        }
        this.allNodeClickEvent = {};
    }
    protected offTouch(node: cc.Node): void {
        if (!node) {
            return;
        }
        if (this.allNodeClickEvent[node.uuid]) {
            let touchHandler = this.allNodeClickEvent[node.uuid].callback;
            let target = this.allNodeClickEvent[node.uuid].target;
            node.off('click', touchHandler, target);
            delete this.allNodeClickEvent[node.uuid];
            if (node.getComponent(cc.Button)) {
                node.getComponent(cc.Button).interactable = false;
                node.getComponent(cc.Button).enableAutoGrayEffect = true;
            }
        }
    }

    protected CreateListItem(content: cc.Node, itemNode: any, data: Array<any>, Base: typeof BaseItem) {
        let child = content.children;
        for (let i = 0; i < data.length; i++) {
            if (!child[i]) {
                let item = cc.instantiate(itemNode);
                content.addChild(item);
            }
            child[i].active = true;
            if (!child[i].getComponent(Base)) {
                child[i].addComponent(Base);
                child[i].getComponent(Base).init();
            }
            child[i].getComponent(Base).setData(data[i]);
        }
        if (child.length > data.length) {
            for (let i = data.length; i < child.length; i++) {
                child[i].active = false;
            }
        }
    }

    // update (dt) {}
}
