    ON_LEVEL_DATA_UPDATE(res) {
        if (res.data) {
            this.data = res.data;
            DataManager.instance.levelData = res.data.round_map;
            GlobalVar.userInfo.round = res.data.round;
            GlobalVar.userInfo.special_round = res.data.special_round;
            this.initPanel();
        }
    }


