    initPanel() {
        if (this.panelType == null) {
            this.panelType = PanelType.Putong
            DataManager.instance.selectType = PanelType.Putong;
        }
        if (this.panelType == PanelType.Putong) {

            this.mode.spriteFrame = SpriteManager.ModelSprite[0];
            // this.removeItem();
            let arr = this.convertTo2DArray(DataManager.instance.levelData);
            this.scorllView.getComponent(LoopScrollView).setData(arr);
            let num = Math.floor(GlobalVar.userInfo.round / 4);
            let pro = 1 - num / arr.length;
            this.scorllView.scrollTo(cc.v2(0, pro), 0.5);
            console.log(pro, "百分比")
            // this.CreateListItem(this.content, this.levelItemPuTong, DataManager.instance.levelData, LevelItemPuTong)
        } else {

            this.mode.spriteFrame = SpriteManager.ModelSprite[1];
            let arr = this.convertTo2DArray(DataManager.instance.levelData);
            this.scorllView.getComponent(LoopScrollView).setData(arr);
            let num = Math.floor((GlobalVar.userInfo.special_round) / 4);
            let pro = 1 - num / arr.length;
            this.scorllView.scrollTo(cc.v2(0, pro), 0.5);
            console.log(pro, "百分比")
            // this.removeItem();
            // this.CreateListItem(this.content, this.levelItemTiaoZhan, DataManager.instance.levelData, LevelItemTiaoZhan)
        }
    }
    onCloseClick() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, false)
    }
    /**点击普通模式 */
    onPuTongClick() {
        if (this.panelType == PanelType.Putong) {
            return;
        }
        this.scorllView.stopAutoScroll();
        this.scorllView.scrollToTop();
        this.labelPuTong.active = true;
        this.labelTiaoZhan.active = false;
        this.panelType = PanelType.Putong;
        DataManager.instance.selectType = PanelType.Putong;
        this.initPanel();
    }
    /**点击挑战模式 */
    onTiaoZhanClick() {
        if (this.panelType == PanelType.Tiaozhan) {
            return;
        }
        this.scorllView.stopAutoScroll();
        this.scorllView.scrollToTop();
        this.labelPuTong.active = false;
        this.labelTiaoZhan.active = true;
        this.panelType = PanelType.Tiaozhan;
        DataManager.instance.selectType = PanelType.Tiaozhan;
        this.initPanel();
    }
