    private removeItem() {
        let child = this.content.children;
        for (let i = child.length - 1; i >= 0; i--) {
            PoolManager.instance.putNode(child[i]);
        }
    }
    convertTo2DArray(inputArray: any[]): any[][] {
        const result: any[][] = [];
        // 遍历原始数组
        for (let i = 0; i < inputArray.length; i += 4) {
            // 从当前位置 i 提取 4 个元素，并创建新的子数组
            const chunk = inputArray.slice(i, i + 4);
            result.push(chunk); // 将提取的子数组添加到结果数组中
        }
        return result;
    }
