import { GameEvent } from "../data/EventName";
import { GoodType } from "../data/InterfaceCom";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BuyCom extends cc.Component {

    

    goods: GoodType = null;
    price: string = ""



    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_END,this.onClick,this);
    }

    protected onDestroy(): void {
        this.node.off(cc.Node.EventType.TOUCH_END,this.onClick,this);
    }

    start () {

    }

    onClick(){

        cc.game.emit(GameEvent.BUY_GOODS,this.goods,this.price);
    }

    // update (dt) {}
}
