import { GameEvent } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { LevelData } from "../data/InterfaceCom";
import { ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import ToastManager from "../manager/ToastManager";
import { HttpCom } from "../net/HttpCom";
import { StaticInstance } from "../StaticInstance";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;

@ccclass
export default class WinPTLayer extends BaseLanguageLayer {
    private buttons: cc.Node = null;
    private btnShare: cc.Node = null;
    private btnNext: cc.Node = null;
    private cionLabel: cc.Label = null;
    private btnClose: cc.Node = null;
    onLoad(): void {
        super.onLoad();
        this.buttons = cc.find('style/panel/buttons', this.node);
        this.btnShare = cc.find('btn_share', this.buttons);
        this.btnNext = cc.find('btn_next', this.buttons);
        this.btnClose = cc.find('bgshade', this.node);
        this.cionLabel = cc.find('style/panel/cionLabel', this.node).getComponent(cc.Label);
        this.onTouch(this.btnShare, this.onShare, this);
        this.onTouch(this.btnNext, this.onNext, this);
        this.onTouch(this.btnClose, this.onClose, this, 1);
    }

    show(): void {
        super.show();
        this.node.active = false;
        this.initPanel();
        console.log(DataManager.instance.level, "当前关卡")
        console.log(GlobalVar.maxLevel, "最大关卡")
        if (DataManager.instance.level >= GlobalVar.maxLevel) {
            this.btnNext.active = false;
        } else {
            this.btnNext.active = true;
        }
    }
    private onShare() {
        console.log("点击分享按钮");
        cc.game.emit(GameEvent.OPEN_INVITE_URL);
    }
    private onClose() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.WINPT, false);
        EventManager.instance.emit(EventType.CLOSE_LEVEL_BTN);
        StaticInstance.gameManager.removealllevel();
        // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MAIN_LEVEL, false);
    }
    private onNext() {
        if (!GlobalVar.isWssReady) {
            console.log("wss未连接成功")
            return;
        }
        if (Number(GlobalVar.userInfo.hp.value) < 5) {
            ToastManager.instance.show(Languages['toast1'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
            return
        }
        let level = DataManager.instance.level + 1;
        HttpCom.start(level, 0, (res: any) => {
            let data: LevelData = res.data;
            DataManager.instance.level = level;
            GlobalVar.userInfo.hp.value = String(data.hp.value);
            GlobalVar.userInfo.hp.last_cd_time = data.hp.last_cd_time;
            DataManager.instance.nowRoundConfig = data.round_config;
            DataManager.instance.seconds = data.round_config.time_limit;
            EventManager.instance.emit(EventType.UPDATE_POWER_LABEL);
            DataManager.instance.mode = ENUM_GAME_MODE.TIMER;
            StaticInstance.transitionsManager.play(ENUM_UI_TYPE.WINPT, null, () => {
                StaticInstance.gameManager.onGameLevelStart()
                // if (!DataManager.instance.isWssComplete) {
                //     console.log("wss操作还未成功WinPTLayer")
                //     return;
                // }
                DataManager.instance.isWssComplete = false;
                GlobalVar.wss.set_car_nums(DataManager.instance.carNum);
            })
        })
    }
    private initPanel() {
        HttpCom.end((res) => {
            this.node.active = true;
            let data = res.data;
            this.cionLabel.string = "+" + DataManager.instance.formatBytes(data.rewards.coin);
            DataManager.instance.coin = data.coin;
            GlobalVar.userInfo.hp.value = data.hp.value;
            GlobalVar.userInfo.hp.last_cd_time = data.hp.last_cd_time;
            GlobalVar.userInfo.key = data.key;
            EventManager.instance.emit(EventType.UPDATE_COIN_LABEL);
        })
        // DataManager.instance.coin += 20;
        // DataManager.instance.save();
        // EventManager.instance.emit(EventType.UPDATE_COIN_LABEL);
    }
}