
import { GlobalVar } from '../data/GlobalVar';
import { Config } from '../data/Config';
import { GameEvent, S2C_WssEvent, WssEvent } from '../data/EventName';
import { HttpCom } from './HttpCom';
import ToastManager from '../manager/ToastManager';
import DataManager from '../manager/DataManager';
import EventManager, { EventType } from '../manager/EventManager';
import { Languages } from '../Languages';
const { ccclass, property } = cc._decorator;
//code代码含义
//错误消息
// 2000  token解析错误
// 2001  玩家其他设备登陆
// 2003  sign签名错误
// 2004  关卡信息读取错误（可能已经结束，没开始，等等）
// 2005  参数错误


// //成功消息
// 2002  玩家socket连接成功   
// 2006  设置关卡 汽车数量成功
// 2007  移动车辆成功
// 2008  小车碰撞成功
@ccclass('WssCom')
export class WssCom {

  socket: WebSocket = null;
  intervalId: any = null;
  start() {

  }

  initWs() {
    //const token = "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJsb2dpblR5cGUiOiJsb2dpbiIsImxvZ2luSWQiOiJhcGlfdXNlcjoxMDAwMTEiLCJyblN0ciI6IlBNU2N1R2J5QzJVWGxyRjM2RjZpODVSMVhkWDRVdlR0IiwiYXBpVXNlcklkIjoxMDAwMTF9.GjZ8QjoeuVi6QBLQ-Q5Z4TQyAhyph-ga_HzK-inbYZU";


    let token = encodeURIComponent(GlobalVar.token);
    console.log(GlobalVar.token, "GlobalVar.token")
    console.log(token, "token")
    let sign = HttpCom.generateSignature({ token: GlobalVar.token });
    console.log(sign, "sign")
    let wssUrl = Config.wss + `?token=${token}&sign=${sign}`

    console.log(wssUrl, "wssUrl")
    this.socket = new WebSocket(wssUrl);

    this.socket.onclose = (() => {
      console.log("ws关闭")
      //TODO 翻译
      GlobalVar.isWssReady = false;
      clearInterval(this.intervalId);
      ToastManager.instance.show(Languages['toast6'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
      cc.game.emit(S2C_WssEvent.ON_WSS_CLOSE)
      console.log("ping定时器已停止");
    });
    this.socket.onerror = ((ev) => {
      console.log("ws错误")
      console.log(ev)
      cc.game.emit(S2C_WssEvent.ON_WSS_ERROR)
      GlobalVar.isWssReady = false;
    })
    this.socket.onmessage = ((event) => {
      if (event.data === "PONG") {

        return;
      }
      console.log('Received message:', event.data);

      let obj = JSON.parse(event.data);
      // 2000  token解析错误
      // 2001  玩家其他设备登陆
      // 2003  sign签名错误
      // 2004  关卡信息读取错误（可能已经结束，没开始，等等）
      // 2005  参数错误
      // 2010  车辆移动失败
      // 2011  车辆碰撞失败
      // 2002  玩家socket连接成功   
      // 2006  设置关卡 汽车数量成功
      // 2007  移动车辆成功
      // 2008  小车碰撞成功
      // 2009  移动车辆成功奖励
      // 2013  每日签到支付成功返回    {"task_id":17}
      // 2014  购买商品支付成功返回   {"goods_id":1}

      switch (obj.code) {
        case 2000:
        case 2003:
        case 2004:
        case 2005:
          cc.game.emit(S2C_WssEvent.ERROR_MSG, obj);
          break;
        case 2010:
          console.log("车辆移动失败")
          break;
        case 2011:
          console.log("车辆碰撞失败")
          break;
        case 2006:
          console.log("设置车辆数成功")
          cc.game.emit(S2C_WssEvent.ON_SET_CAR_NUM, obj);
          break;
        case 2007:
          console.log("车辆移动成功")
          cc.game.emit(S2C_WssEvent.ON_MOVE_CAR, obj);
          break;
        case 2008:
          console.log("车辆碰撞成功")
          cc.game.emit(S2C_WssEvent.ON_CRASH_CAR, obj);
          break;
        case 2009:
          console.log("移动车辆奖励")
          cc.game.emit(S2C_WssEvent.ON_CAR_REWARD, obj);
          break;
        case 2001:
          console.log("玩家在其他设备登录")
          cc.game.emit(S2C_WssEvent.PLAYER_LOGIN_ON_OTHER_DEVICE, obj)
          break;
        case 2013:
          console.log("每日签到支付成功返回")
          cc.game.emit(S2C_WssEvent.DAILY_SIGN_PAY_RESPON_SUCCESS, obj)
          EventManager.instance.emit(EventType.UPDATE_TASK_PANEL);
          break;
        case 2014:

          //{rewards:{"key":1,"coin":100,"hp":{"value":20,"cd":123123}}}
          //cc.game.emit(S2C_WssEvent.PLAYER_LOGIN_ON_OTHER_DEVICE,obj);
          //TODO 翻译
          console.log(obj, "购买商品支付成功返回")
          ToastManager.instance.show(Languages['toast7'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
          DataManager.instance.coin = obj.data.coin;
          DataManager.instance.keys = obj.data.key;
          GlobalVar.userInfo.hp.value = obj.data.hp.value;
          //EventManager.instance.emit(EventType.UPDATE_TASK_PANEL);
          EventManager.instance.emit(EventType.UPDATE_POWER_LABEL)
          EventManager.instance.emit(EventType.UPDATE_KEY_LABEL);
          EventManager.instance.emit(EventType.UPDATE_COIN_LABEL);
          break;
      }

      //}
    })
    this.socket.onopen = (() => {
      //this.socket.send("PING");
      console.log("ws打开,开始发送心跳")
      //TODO 翻译
      ToastManager.instance.show(Languages['toast8'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
      GlobalVar.isWssReady = true;
      cc.game.emit(GameEvent.ENABLE_WS_CHECK,true);
      cc.game.emit(S2C_WssEvent.ON_WSS_OPEN)
      DataManager.instance.isWssComplete = true;

      if (GlobalVar.isFirst) {
        cc.game.emit(GameEvent.CHECK_NEW_PLAYER)
        GlobalVar.isFirst = false;
      }
      this.intervalId = setInterval(() => {
        this.socket.send("PING");
        //console.log("ping",new Date())
      }, 3000);
    })
  }


  generateRandomString(length) {
    const charset = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";
    let result = '';
    for (let i = 0; i < length; i++) {
      result += charset.charAt(Math.floor(Math.random() * charset.length));
    }
    return result;
  }
  sendMessage(data: any, cb?: Function) {

    // 使用 TextEncoder 将文本转换为二进制
    // const encoder = new TextEncoder();
    // const arrayBuffer = encoder.encode(msg); // 将文本编码为 Uint8Array
    let sign = HttpCom.generateSignature(data)
    data['sign'] = sign;
    let sendJson = JSON.stringify(data);

    this.socket.send(sendJson);
    console.log('Binary message sent:', sendJson);


  }


  set_car_nums(carCount: number, cb?: Function) {

    let data = { act: "set_car_nums", car_nums: carCount, player_id: GlobalVar.userInfo.id }
    this.sendMessage(data, cb);

  }


  move_car(cb?: Function) {
    let data = { act: "move_car", player_id: GlobalVar.userInfo.id }


    this.sendMessage(data, cb);

  }
  crash_car(cb?: Function) {
    let data = { act: "crash_car", player_id: GlobalVar.userInfo.id }


    this.sendMessage(data, cb);

  }



}


