import { GameEvent, PayType } from "../data/EventName";
import { IPayWay, Payment } from "../data/InterfaceCom";
import PayWayItem from "./PayWayItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PayWay extends cc.Component {

    @property(cc.Node)
    tonNode: cc.Node = null;
    @property(cc.Node)
    usdtNode: cc.Node = null;
    @property(cc.Node)
    starNode: cc.Node = null;

    @property(cc.Label)
    goodAmountLab: cc.Label = null;

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        
    }


    
    start () {

    }

    onClick(){
        //cc.game.emit(GameEvent.BUY_GOODS,1,PayType.star)
    }


    init(paymentData: Payment){
        let pay = paymentData.data;

        let payMethod = pay.pay_ways;

        this.goodAmountLab.string = "=$" + pay.goods_amount;
        this.tonNode.active = false;
        this.usdtNode.active = false;
        this.starNode.active = false;
        payMethod.forEach((e,i)=>{
            switch(e.payment_method){

                case PayType.ton:
                    this.tonNode.active = true;
                    this.initPayWay(this.tonNode,e,PayType.ton)
                    this.tonNode.getComponent(PayWayItem).init(PayType.ton,e.amount);
                    break;
                case PayType.usdt:
                    this.usdtNode.active = true;
                    this.initPayWay(this.usdtNode,e,PayType.usdt)
                    this.tonNode.getComponent(PayWayItem).init(PayType.usdt,e.amount);
                    break;
                case PayType.star:
                    this.starNode.active = true;
                    this.initPayWay(this.starNode,e,PayType.star)
                    this.tonNode.getComponent(PayWayItem).init(PayType.star,e.amount);
                    break;
            }
        })


    }  


    initPayWay(node: cc.Node,payWay: IPayWay,wayName: String){

        let offNode = cc.find("off",node);
        let layoutNode = cc.find("layout",node)

        if(payWay.discount == 0){
            offNode.active = false;
            layoutNode.children[0].active = false;

            layoutNode.children[0].getComponent(cc.Label).string = payWay.amount.toString();
            layoutNode.children[1].getComponent(cc.Label).string = (payWay.amount).toString() + wayName;
        }else{
            offNode.active = true;
            offNode.children[0].getComponent(cc.Label).string = (payWay.discount * 100).toString() + wayName;


            layoutNode.children[0].active = true;
            layoutNode.children[0].getComponent(cc.Label).string = payWay.amount.toString();
            layoutNode.children[1].getComponent(cc.Label).string = (payWay.amount * payWay.discount).toString() + wayName;

        }


    }




    onCloseBtnClick(){
        this.node.active = false;

    }

    // update (dt) {}
}
