import { PayType } from "./EventName";

export interface UserInfo {
  id: number;               // 玩家id
  tg_id: number;           // 玩家 telegram id
  username: string;        // 用户名（可能为空）
  full_name: string;       // 玩家全名
  round: number;           // 最大玩家到达的普通关卡
  special_round: number;   // 最大到达的挑战关卡
  coin: number;            // 代币
  hp: {
    value: string;          // 当前体力
    last_cd_time: number;  // 最后一次恢复体力UTC时间戳 0代表满体力，不需要恢复
  };
  key: number;             // 钥匙
  lang: string;            // 当前使用的语言
  skins: number[];         // 玩家拥有的所有皮肤id
  skin_id: number;         // 玩家当前使用的皮肤id
  create_time: string;     // 创建时间（格式为 "yyyy-MM-dd HH:mm:ss"）
  last_login_time: string | null; // 上次登录时间（可能为null）
  max_hp: string;          // 最大体力值
  init_player_hp: string;  // 玩家初始化体力
  hp_recover_cd: string;   // 体力恢复时间，单位秒
  init_player_key: string; // 初始化钥匙
  bot_link: string

}

// 定义关卡列表项的接口
export interface RoundMapItem {
  round: number; // 关卡编号
  rate: number;  // 挑战关卡评分，0 表示玩家还没挑战
}

// 定义整体的数据接口
// https://mc-api.liqul.xyz/api/round/map
interface GameLevels {
  round_map: RoundMapItem[];  // 关卡列表
  round: number;              // 当前玩家普通关卡
  special_round: number;     // 当前玩家挑战关卡
}

// 定义 round_config 的接口
export interface RoundConfig {
  id: number;                     // 关卡 ID
  round: number;                  // 关卡 第几关
  unit: number;                   // 单位，可能表示某种度量单位
  time_limit: number;             // 挑战关卡时 关卡限时（秒）
  punish_time: number;            // 挑战关卡时 碰撞惩罚时间（秒）
  rate_rule: string;              // 挑战关卡时 评分规则（JSON 字符串）
  use_hp: number;                 // 关卡消耗的体力
  rewards: string;                // 完成关卡奖励（JSON 字符串）
  unit_rewards: string;           // 每移除一辆车奖励（JSON 字符串）
  isSpecial: number;              // 是否是特殊关卡（1 表示是，0 表示否）
  timestamp: number;              // 时间戳
  with: number;                   // 关卡宽度
  height: number;                 // 关卡高度
}

// 定义 hp 的接口
export interface HP {
  value: number;                  // 体力值
  last_cd_time: number;           // 上次冷却时间的时间戳
}

// 定义整体的数据接口
//https://mc-api.liqul.xyz/api/round/start
export interface LevelData {
  round_config: RoundConfig;      // 关卡配置
  is_special: number;             // 是否挑战关卡
  skin_id: number;                // 皮肤 ID
  hp: HP;                         // 体力信息
}

// 定义商品道具的接口
export interface GoodItem {
  [key: string]: number; // 商品涵盖的道具，键为道具名称，值为数量
}

// 定义商品的接口
export interface Product {
  good_id: number;       // 商品 ID
  type: string;          // 商品类型
  items: GoodItem;           // 商品涵盖的道具
  is_hot: boolean;       // 是否是热标签
  cost: string;          // 商品价格
}

// 定义整体的响应数据接口
export interface GoodsList {
  code: number;          // 响应代码
  message: string;       // 响应消息
  data: Product[];       // 商品列表
  version: string;       // 数据版本
  timestamp: number;     // 时间戳
}
export interface Rewards {
  coin?: number; // 奖励的类型及其数量
  key?: number;
  hp?: number;
}

export interface GoodType {
  coin?: number; // 奖励的类型及其数量
  key?: number;
  hp?: number;
  skin?: number;
}

export enum TaskModule {
  open_bot = "open_bot",
  tw_content_like = "tw_content_like",
  hp_use_daily = "hp_use_daily",
  move_car_daily = "move_car_daily",
  game_click = "game_click",
  daily_sign = "daily_sign",
  round_finish = "round_finish",
  round_rate_star = "round_rate_star",
  hp_use = "hp_use",
  goods_buy = "goods_buy",
  tg_rss = "tg_rss",
  tw_follow = "tw_follow",
  connect_wallet = "connect_wallet"

}
// 定义任务的接口
export interface Task {
  can_receive: number;     ////能不能领取奖励
  is_receive: boolean;     // 玩家是否已领取奖励
  module: string;      //任务模块名
  rewards: Rewards;        // 任务完成后奖励
  target_num: number;      // 任务达成目标
  task_id: number;         // 任务 ID
  extra?: TaskExtraData;            // 任务类型
  task_process: number;    // 当前玩家任务进度
}

export interface TaskExtraData {
  action: string,
  url: string
}

export interface IPayWay {
  payment_method: PayType;
  amount: number;
  discount: number;
}

export interface PaymentData {
  goods_amount: number;
  goods_currency: string;
  pay_ways: IPayWay[];
}

export interface Payment {
  code: number;
  message: string;
  data: PaymentData;
  version: string;
  timestamp: number;
}




