
import { Config } from "../data/Config";
import { GameEvent } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { ENUM_UI_TYPE } from "../Enum";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import SpriteManager from "../manager/SpriteManager";
import { HttpCom } from "../net/HttpCom";
import { StaticInstance } from "../StaticInstance";
import BaseItem from "./BaseItem";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;


enum PanelType {
    HomePage = 1,
    ViewPage,
}

const rewardConfig = [
    { status: 1, key: 3, coin: 3, people: 4 },
    { status: 1, key: 20, coin: 20, people: 8 },
    { status: 1, key: 40, coin: 40, people: 16 },
    { status: 1, key: 60, coin: 60, people: 20 },
]
const inviteeConfig = [
    "1523678", "1523678", "1523678", "1523678", "1523678", "1523678",
]

class InviteeItem extends BaseItem {
    idLabel: cc.Label = null;
    init(): void {
        this.idLabel = this.node.getChildByName("id").getComponent(cc.Label);
    }
    setData(data: any): void {
        this.idLabel.string = "ID:" + data;
    }
}

class ShareRewardItem extends BaseItem {
    diSprite: cc.Sprite = null;
    keyNum: cc.Label = null;
    coinNum: cc.Label = null;
    keLingDi: cc.Node = null;
    btnLabel: cc.Label = null;
    nowData: any = null;
    init(): void {
        this.diSprite = this.node.getComponent(cc.Sprite);
        this.keyNum = this.node.getChildByName("keyslabel").getComponent(cc.Label);
        this.coinNum = this.node.getChildByName("coinlabel").getComponent(cc.Label);
        this.keLingDi = this.node.getChildByName("kelingqvdi");
        this.btnLabel = this.node.getChildByName("statuslabel").getComponent(cc.Label);
    }
    setData(data: any): void {
        console.log(data, "-----------------")
        this.nowData = data;
        this.keyNum.string = data.rewards.key;
        this.coinNum.string = data.rewards.coin;
        this.offTouch(this.node);
        if (data.can_receive) {//可领取
            this.keLingDi.active = true;
            this.btnLabel.string = Languages["kelingqv"][DataManager.instance.language];
            this.diSprite.spriteFrame = SpriteManager.shareRewardDi[0];
            this.onTouch(this.node, this.onTouchNode, this);
        } else if (data.is_receive) {//已领取
            this.keLingDi.active = false;
            this.btnLabel.string = Languages["yilingqv"][DataManager.instance.language];
            this.diSprite.spriteFrame = SpriteManager.shareRewardDi[0];
        } else {
            // this.btnLabel.string = "未完成";
            let str = Languages['yaoqingrenshu'][DataManager.instance.language];
            str = str.replace('{number}', data.target_num.toString());
            this.btnLabel.string = str;
            this.keLingDi.active = false;
            this.diSprite.spriteFrame = SpriteManager.shareRewardDi[1];
        }
    }
    onTouchNode() {
        if (this.nowData.can_receive) {//可领取
            HttpCom.taskReceiveReward(this.nowData.task_id, (res) => {
                console.log(res, "领取任务")
                DataManager.instance.coin = res.data.coin;
                DataManager.instance.keys = res.data.key;
                // GlobalVar.userInfo.hp = res.data.hp;
                EventManager.instance.emit(EventType.UPDATE_SHAHE_PANEL);
                // EventManager.instance.emit(EventType.UPDATE_POWER_LABEL)
                EventManager.instance.emit(EventType.UPDATE_KEY_LABEL);
                EventManager.instance.emit(EventType.UPDATE_COIN_LABEL);
            })
        } else if (this.nowData.is_receive) {//已领取

        } else {//未完成

        }
    }
}

@ccclass
export default class ShareLayer extends BaseLanguageLayer {
    panel1: cc.Node = null;
    panel2: cc.Node = null;
    numLabel: cc.Label = null;
    rewardsContent: cc.Node = null;
    btnLianJie: cc.Node = null;
    btnShare: cc.Node = null;
    content: cc.Node = null;
    inviteeItem: cc.Node = null;
    zanwugengduo: cc.Node = null;
    shareRewardItem: cc.Node = null;
    btnFanHui: cc.Node = null;
    btnCheck: cc.Node = null;
    content1: cc.Node = null;
    panelType: PanelType = null;
    closeBtn: cc.Node = null;
    onLoad(): void {
        super.onLoad();
        this.panel1 = cc.find('style/panel/panel1', this.node);
        this.panel2 = cc.find('style/panel/panel2', this.node);
        this.rewardsContent = cc.find("rewards", this.panel1);
        this.btnShare = cc.find("btnShare", this.panel1);
        this.btnLianJie = cc.find("btnLianjie", this.panel1);
        this.content = cc.find('view/mask/content', this.panel1);
        this.numLabel = cc.find('leijidi/numlabel', this.panel1).getComponent(cc.Label);
        this.inviteeItem = this.node.getChildByName("inviteeItem");
        this.zanwugengduo = this.node.getChildByName("zanwugengduo");
        this.shareRewardItem = this.node.getChildByName("shareRewardItem");
        this.btnFanHui = this.panel2.getChildByName("fanhui");
        this.btnCheck = this.panel1.getChildByName("chakanquanbu");
        this.content1 = cc.find('view/mask/content', this.panel2);
        this.closeBtn = cc.find('style/panel/btn_close', this.node);
        this.onTouch(this.btnShare, this.onTouchShare, this);
        this.onTouch(this.btnLianJie, this.onTouchLianJie, this);
        this.onTouch(this.btnFanHui, this.onTouchFanHui, this);
        this.onTouch(this.btnCheck, this.onTouchCheck, this);
        this.onTouch(this.closeBtn, this.onCloseBtnClick, this);
        EventManager.instance.on(EventType.UPDATE_SHAHE_PANEL, this.initPanel, this);
    }

    onCloseBtnClick() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, false);
    }
    show(): void {
        super.show();
        this.initPanel();
    }
    initPanel() {
        HttpCom.invite((res) => {
            let str = Languages['yaoqingrenshu'][DataManager.instance.language];
            str = str.replace('{number}', res.data.invited_player_ids.length.toString());
            this.numLabel.string = str;
            if (this.panelType == null) {
                this.panelType = PanelType.HomePage;
            }
            if (this.panelType == PanelType.HomePage) {
                this.panel1.active = true;
                this.panel2.active = false;
                this.CreateListItem(this.rewardsContent, this.shareRewardItem, res.data.invite_task, ShareRewardItem)
                this.CreateListItem(this.content, this.inviteeItem, res.data.invited_player_ids, InviteeItem);
            } else {
                this.panel1.active = false;
                this.panel2.active = true;
                this.CreateListItem(this.content1, this.inviteeItem, inviteeConfig, InviteeItem);
            }
        })
    }

    onTouchShare() {
        console.log("点击分享按钮");

        cc.game.emit(GameEvent.OPEN_INVITE_URL);
    }
    onTouchLianJie() {
        // Editor.Clipboard.writeText('text', '复制成功').then(() => {
        //     console.log("复制成功")
        // }).catch(() => {
        //     console.log("复制失败")
        // })
        if (window['Telegram'] && window['Telegram'].WebApp) {
            //window['Telegram'].WebApp.openTelegramLink("https://t.me/share/url?url="+url)

            // const link = "https://t.me/Htbtset2bot" + "/" + Config.appName + `?startapp=${btoa(
            //     JSON.stringify({ inviteCode: GlobalVar.userInfo.id })
            const link = "https://t.me/Htbtset2bot" + "/" + Config.appName + `?startapp=${
               GlobalVar.userInfo.id.toString()
            }`;

            //const shareLink = `https://t.me/share/url?url=${encodeURIComponent(link)}`;
            navigator.clipboard.writeText(link).then(() => {
                console.log("复制成功", link)
            }).catch(() => {
                console.log("复制失败")
            })
        }
    }
    onTouchFanHui() {
        this.panelType = PanelType.HomePage;
        this.initPanel();
    }
    onTouchCheck() {
        this.panelType = PanelType.ViewPage;
        this.initPanel();
    }
}