    TON_DAILY_SIGN() {
        HttpCom.dailySign(17, async (resp) => {
            TonConnectUi.Instance.setWallet(resp.data.ton_address);
            console.log(resp.data, "钱包签到返回的")

            let pl = JSON.stringify({ order_no: resp.data.order_no, sign: HttpCom.generateSignature({ order_no: resp.data.order_no }) })
            if (TonConnectUi.Instance.isTonConnected()) {

                let trans: Transaction = {
                    amount: resp.data.amount,
                    payload: await WebTon.Instance.createMessagePayload(pl),
                    callBack: (result: any) => {
                        console.log(result);
                    }
                }
                TonConnectUi.Instance.sendTransaction(trans)
            } else {
                TonConnectUi.Instance.openModal();
            }
        })
    }

