import { TonConnectUi } from "../cocos-telegram-miniapps/telegram-ui";
import { TelegramWebApp } from "../cocos-telegram-miniapps/telegram-web";
import { GameEvent } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { Task, TaskModule } from "../data/InterfaceCom";
import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE } from "../Enum";
import { Languages, LanguageType } from "../Languages";
import AudioManager from "../manager/AudioManager";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import SpriteManager from "../manager/SpriteManager";
import { HttpCom } from "../net/HttpCom";
import { StaticInstance } from "../StaticInstance";
import BaseItem from "./BaseItem";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;


enum PanelType {
    Daily = 1,
    Target,
}
const RewardConfig = {
    coin: 0,
    key: 1,
    hp: 2,
}
const TaskConfig = {
    round_finish: { desc: "round_finish", icon: 4 },
    round_rate_star: { desc: "round_rate_star", icon: 4 },
    hp_use: { desc: "hp_use", icon: 4 },
    goods_buy: { desc: "goods_buy", icon: 4 },
    tg_rss: { desc: "tg_rss", icon: 4 },
    tw_follow: { desc: "tw_follow", icon: 4 },
    connect_wallet: { desc: "jiarupindao", icon: 4 },

    open_bot: { desc: "open_bot", icon: 0 },
    tw_content_like: { desc: "tw_content_like", icon: 1 },
    hp_use_daily: { desc: "hp_use_daily", icon: 2 },
    move_car_daily: { desc: "move_car_daily", icon: 3 },
    game_click: { desc: "game_click", icon: 3 },
    daily_sign: { desc: "daily_sign", icon: 3 },
}

class TaskRewardItem extends BaseItem {
    icon: cc.Sprite = null;
    numLabel: cc.Label = null;
    init(): void {
        this.icon = this.node.getChildByName("icon").getComponent(cc.Sprite);
        this.numLabel = this.node.getChildByName("numlabel").getComponent(cc.Label);
    }
    setData(data: any): void {
        this.icon.spriteFrame = SpriteManager.taskRewardIcon[RewardConfig[data.key]]
        this.numLabel.string = "x" + data.num;
    }
}

class TaskItem extends BaseItem {
    icon: cc.Sprite = null;
    describeLabel: cc.Label = null;
    btnGo: cc.Node = null;
    progressLabel: cc.Label = null;
    content: cc.Node = null;
    taskRewardItem: cc.Node = null;
    nowData: Task = null;
    isClick: boolean = false;

    init(): void {
        this.icon = this.node.getChildByName("icon").getComponent(cc.Sprite);
        this.describeLabel = this.node.getChildByName("label").getComponent(cc.Label);
        this.btnGo = this.node.getChildByName("btnGo");
        this.content = this.node.getChildByName('rewardList');
        this.progressLabel = this.node.getChildByName("progress").getComponent(cc.Label);
        this.taskRewardItem = cc.find('taskRewardItem', this.node);
        this.addTrendsLabel(this.describeLabel.node)
    }
    onTouchGo() {
        if (!this.isClick) {
            return;
        }
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        console.log(this.nowData.task_id, "任务id");
        if (this.nowData.can_receive == 1) {//可领取
            this.isClick = false;
            HttpCom.taskReceiveReward(this.nowData.task_id, (res) => {
                console.log(res, "领取任务")
                DataManager.instance.coin = res.data.coin;
                DataManager.instance.keys = res.data.key;
                GlobalVar.userInfo.hp = res.data.hp;
                EventManager.instance.emit(EventType.UPDATE_TASK_PANEL);
                EventManager.instance.emit(EventType.UPDATE_POWER_LABEL)
                EventManager.instance.emit(EventType.UPDATE_KEY_LABEL);
                EventManager.instance.emit(EventType.UPDATE_COIN_LABEL);
                this.isClick = true;
                this.offTouch(this.btnGo);
            })
        } else if (this.nowData.is_receive) {//已领取

        } else {



            switch (this.nowData.module) {

                case TaskModule.connect_wallet:
                    console.log("连接钱包任务")
                    if (!TonConnectUi.Instance.isTonConnected()) {
                        TonConnectUi.Instance.openModal();
                    } else {
                        HttpCom.taskProcess(this.nowData.task_id, () => {
                            EventManager.instance.emit(EventType.UPDATE_TASK_PANEL);
                        });
                    }
                    break;

                case TaskModule.daily_sign:
                    console.log("每日钱包签到任务")
                    cc.game.emit(GameEvent.TON_DAILY_SIGN);
                    break;
                case TaskModule.game_click:
                case TaskModule.open_bot:
                case TaskModule.tw_content_like:
                case TaskModule.tg_rss:
                case TaskModule.tw_follow:
                    console.log("连接钱包任务")
                    if (this.nowData.extra) {
                        if (this.nowData.extra.action == "jump") {
                            TelegramWebApp.Instance.openTelegramLink(this.nowData.extra.url);
                            HttpCom.taskProcess(this.nowData.task_id, () => {
                                EventManager.instance.emit(EventType.UPDATE_TASK_PANEL);
                            });


                        }
                    }
                    break;
                case TaskModule.goods_buy:
                    console.log("购买商品任务")

                    StaticInstance.transitionsManager.panelJumpTo(ENUM_UI_TYPE.TASK, ENUM_UI_TYPE.SHOP)
                    break;
                case TaskModule.hp_use: ;
                case TaskModule.move_car_daily:
                case TaskModule.hp_use_daily:
                case TaskModule.round_finish:
                case TaskModule.round_rate_star:
                    console.log("跳转关卡任务")
                    StaticInstance.transitionsManager.panelJumpTo(ENUM_UI_TYPE.TASK, ENUM_UI_TYPE.LEVEL_SELECT)
                    break;





            }

        }
    }
    setData(data: Task): void {
        this.nowData = data;
        this.onTouch(this.btnGo, this.onTouchGo, this);
        this.isClick = true;
        this.icon.spriteFrame = SpriteManager.taskItemIcon[TaskConfig[data.module].icon];
        let str = Languages[TaskConfig[data.module].desc][DataManager.instance.language]
        if (data.module == "round_finish") {
            let num = data.target_num * 10;
            str = str.replace('{number}', num.toString());
            this.describeLabel.string = str;
        } else if (data.module == "round_rate_star") {
            str = str.replace('{number}', data.target_num.toString());
            this.describeLabel.string = str;
        } else if (data.module == "hp_use") {
            str = str.replace('{number}', data.target_num.toString());
            this.describeLabel.string = str;
        } else if (data.module == "hp_use_daily") {
            str = str.replace('{number}', data.target_num.toString());
            this.describeLabel.string = str;
        } else if (data.module == "move_car_daily") {
            str = str.replace('{number}', data.target_num.toString());
            this.describeLabel.string = str;
        } else {
            this.describeLabel.string = Languages[TaskConfig[data.module].desc][DataManager.instance.language]
        }
        let arr = [];
        for (let key in data.rewards) {
            arr.push({ key: key, num: data.rewards[key] })
        }
        this.CreateListItem(this.content, this.taskRewardItem, arr, TaskRewardItem);
        // if (data.module == "round_finish") {

        // } else {

        // }
        this.progressLabel.string = data.task_process + "/" + data.target_num;
        if (data.is_receive) {
            this.btnGo.getChildByName("label").getComponent(cc.Label).string = Languages["yilingqv"][DataManager.instance.language];
            this.offTouch(this.btnGo);
        } else if (data.can_receive) {
            this.btnGo.getChildByName("label").getComponent(cc.Label).string = Languages["kelingqv"][DataManager.instance.language];

        } else {
            this.btnGo.getChildByName("label").getComponent(cc.Label).string = "Go";

        }
        // if (!this.describeLabel.node["languageKey"]) {
        //     this.describeLabel.node.attr({ languageKey: data.describe })
        // } else {
        //     this.describeLabel.node["languageKey"] = data.describe;
        // }
    }
}
@ccclass
export default class TaskLayer extends BaseLanguageLayer {
    panel: cc.Node = null;
    taskItem: cc.Node = null;
    content: cc.Node = null;
    btnDaily: cc.Node = null;
    btnTarget: cc.Node = null;
    panelType: PanelType = null;
    mode: cc.Sprite = null;

    closeBtn: cc.Node = null;

    onLoad(): void {
        super.onLoad();
        this.panel = cc.find('style/panel', this.node);
        this.taskItem = cc.find('taskItem', this.node);
        this.content = cc.find('view/content', this.panel);
        this.mode = cc.find("btn", this.panel).getComponent(cc.Sprite);
        this.btnDaily = cc.find('btnmeiri', this.mode.node);
        this.btnTarget = cc.find('btnmubiao', this.mode.node);
        this.closeBtn = cc.find('style/panel/btn_close', this.node);
        this.onTouch(this.btnDaily, this.onTouchDaily, this);
        this.onTouch(this.btnTarget, this.onTouchTarget, this);
        this.onTouch(this.closeBtn, this.onCloseBtnClick, this);
        EventManager.instance.on(EventType.UPDATE_TASK_PANEL, this.updatePanel, this);
    }

    onCloseBtnClick() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, false);
    }


    show(): void {
        super.show();
        this.initPanel();
    }
    initPanel() {
        if (this.panelType == null) {
            this.panelType = PanelType.Daily
        }
        if (this.panelType == PanelType.Daily) {
            HttpCom.taskList("daily", (res) => {
                this.mode.spriteFrame = SpriteManager.taskSelectIcon[0];
                this.CreateListItem(this.content, this.taskItem, res.data, TaskItem)
            })
        } else {
            HttpCom.taskList("resident", (res) => {
                this.mode.spriteFrame = SpriteManager.taskSelectIcon[1];
                this.CreateListItem(this.content, this.taskItem, res.data, TaskItem)
            })
        }
    }
    updatePanel() {
        this.initPanel();
    }
    onTouchDaily() {
        if (this.panelType == PanelType.Daily) {
            return;
        }
        this.panelType = PanelType.Daily;
        this.initPanel();
    }
    onTouchTarget() {
        if (this.panelType == PanelType.Target) {
            return;
        }
        this.panelType = PanelType.Target;
        this.initPanel();
    }
}