    // 发送 GET 请求的方法
    public static get(url: string, callback: (response: any) => void) {
        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && (xhr.status >= 200 && xhr.status < 300)) {
                try {
                    let response = JSON.parse(xhr.responseText);
                    callback(response);
                    console.info("code:", response.code, "msg:", response.msg);
                } catch (e) {
                    console.error("Failed to parse response JSON:", e);

                }
            }

            if (xhr.status == 500) {

            }
        };
        xhr.open("GET", url, true);
        xhr.setRequestHeader("Authorization", "Bearer " + GlobalVar.token); // 设置 Header
        xhr.send();
    }

    // 发送 POST 请求的方法
    public static post(url: string, data: any, callback: (response: any) => void) {
        cc.game.emit(GameEvent.SHOW_LOADING,true)
        let xhr = new XMLHttpRequest();
        xhr.timeout = 10000;
        GlobalVar.postPing = new Date().getTime();
        xhr.ontimeout =  ()=> {
            //cc.game.emit(GameEvent.SHOW_LOADING,false);
            //超时重新请求
            this.post(url,data,callback);
            
        };
    
        // xhr.onerror = function () {
        //     cc.game.emit(GameEvent.SHOW_LOADING,false)
        // };
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && (xhr.status >= 200 && xhr.status < 300)) {
                try {
                    cc.game.emit(GameEvent.SHOW_LOADING,false)
                    console.log(xhr.responseText, "responseText")
                    let response = JSON.parse(xhr.responseText);
                    if(response.code == 0){
                        if(response.header && response.header.Authorization){
                            console.log("token过期，从后端返回中重新获取token")
                            GlobalVar.token = response.header['Authorization'].replace(/^Bearer\s+/, '')
                        }

                        callback(response);
                    }else{
                        //TODO 翻译
                        if(response.code == 9000){
                            ToastManager.instance.show(response.message, { gravity: 'CENTER',duration: 5, bg_color: cc.color(226, 69, 109, 255) })
                        }
                    }
                    console.info("code:", response.code, "msg:", response.msg);
                } catch (e) {
                    console.error("Failed to parse response JSON:", e);
                }
            }
        };
        console.log(url, "请求地址")
        this.serialize_id++;
        data['serialize_id'] = this.serialize_id
        let sign = this.generateSignature(data);
        data['sign'] = sign;
        console.log(data);
        xhr.open("POST", url, true);
        xhr.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
        xhr.setRequestHeader("Authorization", "Bearer " + GlobalVar.token); // 设置 Header
        let str = JSON.stringify(data);
        console.log(str, "postData")
        xhr.send(str);
    }


