import { GlobalVar } from "../data/GlobalVar";
import { PanelType } from "../Index";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import PoolManager from "../manager/PoolManager";
import ScrollViewItemBase from "../manager/ScrollViewItemBase";
import SpriteManager from "../manager/SpriteManager";
import ToastManager from "../manager/ToastManager";
import BaseItem from "./BaseItem";


/**普通关卡 */
class LevelItemPuTong extends BaseItem {
    levelLabel: cc.Label = null;
    sprite: cc.Sprite = null;
    nowData: any = null;
    unLock: cc.Node = null;
    init(): void {
        this.levelLabel = this.node.getChildByName('label').getComponent(cc.Label);
        this.unLock = this.node.getChildByName("weijiesuo");
        this.sprite = this.node.getComponent(cc.Sprite);
        this.onTouch(this.node, this.onTouchClick, this);
    }
    setData(data: any): void {
        this.nowData = data;
        this.levelLabel.string = data.round;
        if (this.nowData.round <= GlobalVar.userInfo.round) {//已完成
            this.sprite.spriteFrame = SpriteManager.custemIcon[1];
            this.unLock.active = false;
        } else if (this.nowData.round == GlobalVar.userInfo.round + 1) {//已开启
            this.sprite.spriteFrame = SpriteManager.custemIcon[0];
            this.unLock.active = false;
        } else {//未解锁
            this.sprite.spriteFrame = SpriteManager.custemIcon[0];
            this.unLock.active = true;
        }
    }
    private onTouchClick() {
        console.log(this.nowData)
        if (this.nowData.round > GlobalVar.userInfo.round + 1) {
            ToastManager.instance.show(Languages['tips1'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
            return;
        }
        EventManager.instance.emit(EventType.GOTO_LEVEL, PanelType.Putong, this.nowData.round)
    }
}
