import { S2C_WssEvent } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE } from "../Enum";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import SpriteManager from "../manager/SpriteManager";
import { StaticInstance } from "../StaticInstance";
import { formatSeconds } from "../Utils";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LevelUILayer extends BaseLanguageLayer {
    btnDown: cc.Node = null;
    btnZhuanQv: cc.Node = null;
    btnRenwu: cc.Node = null;
    btnShop: cc.Node = null;
    btnShare: cc.Node = null;
    btnUp: cc.Node = null;
    btnPower: cc.Node = null;
    btnKey: cc.Node = null;
    btnToken: cc.Node = null;
    powerLabel: cc.Label = null;
    keyLabel: cc.Label = null;
    coinLabel: cc.Label = null;
    timerLabel: cc.Label = null;

    onLoad() {
        super.onLoad();
        this.btnDown = cc.find('btndown', this.node);
        this.btnZhuanQv = cc.find('btnzhuanqv', this.btnDown);
        this.btnRenwu = cc.find('btnrenwu', this.btnDown);
        this.btnShop = cc.find('btnshop', this.btnDown);
        this.btnShare = cc.find('btnshare', this.btnDown);
        this.btnUp = cc.find('btnup', this.node);
        this.btnPower = cc.find('tili', this.btnUp);
        this.btnKey = cc.find('yaoshi', this.btnUp);
        this.btnToken = cc.find('daibi', this.btnUp);
        this.powerLabel = cc.find('tililabel', this.btnUp).getComponent(cc.Label);
        this.keyLabel = cc.find('yaoshilabel', this.btnUp).getComponent(cc.Label);
        this.coinLabel = cc.find('daibilabel', this.btnUp).getComponent(cc.Label);
        this.timerLabel = cc.find('timer', this.btnUp).getComponent(cc.Label);
        this.onTouch(this.btnRenwu, this.onRenWuClick, this);
        this.onTouch(this.btnShop, this.onShopClick, this);
        this.onTouch(this.btnShare, this.onShareClick, this);
        this.onTouch(this.btnZhuanQv, this.onZhuanQvClick, this);
        this.onTouch(this.btnPower, this.onPowerClick, this);
        this.onTouch(this.btnKey, this.onKeyClick, this);
        this.onTouch(this.btnToken, this.onTokenClick, this);
        EventManager.instance.on(EventType.UPDATE_POWER_LABEL, this.updatePowerLabel, this)
        EventManager.instance.on(EventType.UPDATE_KEY_LABEL, this.updateKeyLabel, this);
        EventManager.instance.on(EventType.UPDATE_COIN_LABEL, this.updateCoinLabel, this);
        cc.game.on(S2C_WssEvent.ON_CAR_REWARD, this.onGetReward, this);
    }
    show(): void {
        super.show();
        this.updatePowerLabel();
        this.updateKeyLabel();
        this.updateCoinLabel();
        let time = Date.now();
        console.log(time, "当前时间");
    }

    onGetReward(data) {
        // data = JSON.parse(data);
        let rewards = data.data;
        console.log(rewards)
        DataManager.instance.coin = rewards['coin'];
        this.updateCoinLabel();
    }

    private updateTimer() {
        DataManager.instance.lastPowerRefreshTime -= 1;
        this.timerLabel.string = formatSeconds(DataManager.instance.lastPowerRefreshTime, 'i:s')
        if (DataManager.instance.lastPowerRefreshTime <= 0) {
            GlobalVar.userInfo.hp.value = String(Number(GlobalVar.userInfo.hp.value) + 1);
            DataManager.instance.lastPowerRefreshTime = Number(GlobalVar.userInfo.hp_recover_cd);
            this.powerLabel.string = GlobalVar.userInfo.hp.value;
            if (Number(GlobalVar.userInfo.hp.value) >= Number(GlobalVar.userInfo.max_hp)) {
                GlobalVar.userInfo.hp.value = GlobalVar.userInfo.max_hp;
                this.timerLabel.node.active = false;
                this.unschedule(this.updateTimer);
                this.isschedule = false;
            }
        }
        DataManager.instance.lastPowerUpdateTime = new Date().getTime()
        DataManager.instance.save()
    }

    private isschedule: boolean = false;
    private updatePowerLabel() {
        this.powerLabel.string = GlobalVar.userInfo.hp.value;
        if (Number(GlobalVar.userInfo.hp.value) >= Number(GlobalVar.userInfo.max_hp)) {
            this.timerLabel.node.active = false;
            this.unschedule(this.updateTimer);
            this.isschedule = false;
        } else {
            this.timerLabel.node.active = true;
            if (this.isschedule) {
                return;
            }
            let nowTime = Date.now();
            DataManager.instance.lastPowerRefreshTime = Number(GlobalVar.userInfo.hp_recover_cd) - (Math.floor(nowTime / 1000) - GlobalVar.userInfo.hp.last_cd_time);
            this.schedule(this.updateTimer, 1);
            this.isschedule = true;
        }
    }
    private updateKeyLabel() {
        this.keyLabel.string = `${DataManager.instance.keys}`;
    }
    private updateCoinLabel() {
        this.coinLabel.string = DataManager.instance.formatBytes(DataManager.instance.coin);
        // this.coinLabel.string = `${DataManager.instance.coin}`;
    }

    start() {

    }
    onZhuanQvClick() {//赚取
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, true);
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, false)
    }
    onPowerClick() {//体力
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHOP)) {
            return;
        }
        this.onShopClick();
    }
    onKeyClick() {//钥匙
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHOP)) {
            return;
        }
        this.onShopClick();
    }
    onTokenClick() {//代币
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.TASK)) {
            return;
        }
        this.onRenWuClick()
    }
    onRenWuClick() {//任务
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.TASK)) {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, false)
            // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, true);
        } else {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, true)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, false);
        }
        this.updateBtnState();
    }
    onShopClick() {//商店
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHOP)) {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false)
            // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, true);
        } else {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, true)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, false);
        }
        this.updateBtnState();
    }
    onShareClick() {//邀请
        if (StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHARE)) {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, false)
            // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, true);
        } else {
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHARE, true)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASK, false)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT, false);
        }
        this.updateBtnState();
    }

    private updateBtnState() {
        let index = StaticInstance.uiManager.isActive(ENUM_UI_TYPE.TASK) ? 1 : 0;
        this.btnRenwu.getComponent(cc.Sprite).spriteFrame = SpriteManager.taskIcon[index];
        index = StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHOP) ? 1 : 0;
        this.btnShop.getComponent(cc.Sprite).spriteFrame = SpriteManager.shopIcon[index];
        index = StaticInstance.uiManager.isActive(ENUM_UI_TYPE.SHARE) ? 1 : 0;
        this.btnShare.getComponent(cc.Sprite).spriteFrame = SpriteManager.shareIcon[index];
    }

    // update (dt) {}
}
