import { GameEvent } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { Payment } from "../data/InterfaceCom";
import { ENUM_UI_TYPE } from "../Enum";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import PoolManager from "../manager/PoolManager";
import SpriteManager from "../manager/SpriteManager";
import { HttpCom } from "../net/HttpCom";
import PayWay from "../pay/PayWay";
import { StaticInstance } from "../StaticInstance";
import BaseItem from "./BaseItem";
import BaseLanguageLayer from "./BaseLanguageLayer";


const { ccclass, property } = cc._decorator;


const LiBaoConfig = {
    1: { name: "huasuanlibao" }
    // { type: 1, name: "huasuanlibao", price: 29, reward: [] },
    // { type: 2, name: "chaojilibao", price: 99, reward: [] },
    // { type: 3, name: "pifulibao", price: 29, reward: [] },
}
const RewardConfig = {
    coin: 0,
    hp: 1,
    key: 2,
}
const KeyConfig = [
    { keys: 10, price: 10, spriteId: 1 }, { keys: 20, price: 20, spriteId: 1 }, { keys: 40, price: 40, spriteId: 1 },
    { keys: 50, price: 50, spriteId: 1 }, { keys: 100, price: 99, spriteId: 1 }, { keys: 200, price: 189, spriteId: 1 },
]
const PowerConfig = [
    { power: 10, price: 10, spriteId: 0 }, { power: 20, price: 20, spriteId: 0 }, { power: 40, price: 40, spriteId: 0 },
    { power: 50, price: 50, spriteId: 0 }, { power: 100, price: 99, spriteId: 0 }, { power: 200, price: 189, spriteId: 0 },
]

enum PanelType {
    LiBao = 1,
    DaoJu,
    Skin,
}
interface IKeyItem {
    keys: number,
    price: number,
    spriteId: number,
}
interface IPowerItem {
    power: number,
    price: number,
    spriteId: number,
}

class LiBaoItem extends BaseItem {
    nameLabel: cc.Label = null;
    priceLabel: cc.Label = null;
    btnBuy: cc.Node = null;
    xianGou: cc.Node = null;
    hot: cc.Node = null;
    content: cc.Node = null;
    rewardItem: cc.Node = null;
    nowData: any = null;
    init(): void {
        this.nameLabel = this.node.getChildByName("namelabel").getComponent(cc.Label);
        this.btnBuy = this.node.getChildByName("btnBuy");
        this.priceLabel = this.btnBuy.getChildByName("pricelabel").getComponent(cc.Label);
        this.xianGou = this.node.getChildByName("xiangou");
        this.hot = this.node.getChildByName("hot");
        this.content = this.node.getChildByName("content");
        this.rewardItem = cc.find('rewardItem', this.node);
        this.onTouch(this.btnBuy, this.onTouchBuy, this);
    }
    setData(data: any): void {
        console.log(data, "商品信息")
        this.nameLabel.string = Languages[LiBaoConfig[data.goods_id].name][DataManager.instance.language];
        this.priceLabel.string = "$ " + data.amount;
        if (data.is_hot) {
            this.hot.active = true;
        } else {
            this.hot.active = false;
        }
        let arr = [];
        for (let key in data.items) {
            arr.push({ key: key, value: data.items[key] })
        }
        this.CreateListItem(this.content, this.rewardItem, arr, RewardItem);
        if (data.limit_type == 1) {
            this.xianGou.active = true;
        } else {
            this.xianGou.active = false;
        }

        this.nowData = data;
    }
    onTouchBuy() {
        console.log(this.nowData, "当前商品数据")
        cc.game.emit(GameEvent.SHOW_PAYWAY_PAGE, this.nowData.goods_id);
    }
}
class KeyItem extends BaseItem {
    icon: cc.Sprite = null;
    numLabel: cc.Label = null;
    btnBuy: cc.Node = null;
    priceLabel: cc.Label = null;
    init(): void {
        this.icon = this.node.getChildByName("icon").getComponent(cc.Sprite);
        this.numLabel = this.node.getChildByName("num").getComponent(cc.Label);
        this.btnBuy = this.node.getChildByName("btnBuy");
        this.priceLabel = this.btnBuy.getChildByName("pricelabel").getComponent(cc.Label);
        this.onTouch(this.btnBuy, this.onTouchBuy, this);
    }
    setData(data: IKeyItem): void {
        this.icon.spriteFrame = SpriteManager.shopRewardIcon[data.spriteId];
        this.numLabel.string = "X" + data.keys;
        this.priceLabel.string = "$ " + data.price;
    }
    onTouchBuy() {
        cc.game.emit(GameEvent.SHOW_PAYWAY_PAGE, 1);
    }
}
class RewardItem extends BaseItem {
    icon: cc.Sprite = null;
    numLabel: cc.Label = null;
    init(): void {
        this.icon = this.node.getChildByName("icon").getComponent(cc.Sprite);
        this.numLabel = this.node.getChildByName("num").getComponent(cc.Label);
    }
    setData(data: any): void {
        this.icon.spriteFrame = SpriteManager.shopRewardIcon[RewardConfig[data.key]];
        this.numLabel.string = "x" + data.value;
    }
}
class DaoJuItem extends BaseItem {
    private rewardList: cc.Node = null;
    private btnBuy: cc.Node = null;
    private priceLabel: cc.Label;
    private rewardItem: cc.Node = null;
    private nowData: any = null;
    init(): void {
        this.btnBuy = this.node.getChildByName("btnBuy");
        this.priceLabel = this.btnBuy.getChildByName("pricelabel").getComponent(cc.Label);
        this.rewardList = this.node.getChildByName("rewardList");
        this.rewardItem = cc.find('rewardItem', this.node);
        this.onTouch(this.btnBuy, this.onTouchBuy, this);
    }
    setData(data: any): void {
        this.nowData = data;
        console.log(data, "-----------")
        this.priceLabel.string = "$ " + data.amount;
        let arr = [];
        for (let key in data.items) {
            arr.push({ key: key, value: data.items[key] })
        }
        this.CreateListItem(this.rewardList, this.rewardItem, arr, RewardItem);
    }
    onTouchBuy() {
        console.log(this.nowData, "当前商品数据")

        cc.game.emit(GameEvent.SHOW_PAYWAY_PAGE, this.nowData.goods_id);
    }
}

@ccclass
export default class ShopLayer extends BaseLanguageLayer {
    panel: cc.Node = null;
    btnLayout: cc.Node = null;
    btnLiBao: cc.Node = null;
    btnDaoJu: cc.Node = null;
    btnSkin: cc.Node = null;
    libaoItem: cc.Node = null;
    daojuItem: cc.Node = null;
    content: cc.Node = null;
    comingSoon: cc.Node = null;
    star: cc.Node = null;
    panelType: PanelType = null;
    payWayPage: cc.Node = null;
    closeBtn: cc.Node = null;
    onLoad(): void {
        super.onLoad();
        this.panel = cc.find('style/panel', this.node)
        this.btnLayout = cc.find('bg1/btnlayout', this.panel);
        this.btnLiBao = cc.find('btnlibao', this.btnLayout);
        this.btnDaoJu = cc.find('btndaoju', this.btnLayout);
        this.btnSkin = cc.find('btnpifu', this.btnLayout);
        this.libaoItem = cc.find('libaoItem', this.node);
        this.daojuItem = cc.find('daojuItem', this.node);
        this.content = cc.find('view/mask/content', this.panel);
        this.comingSoon = cc.find('style/panel/jingqingqidai', this.node);
        this.star = cc.find('style/panel/xingqiu', this.node);
        this.payWayPage = cc.find('payWay', this.node);

        this.closeBtn = cc.find('style/panel/btn_close', this.node);
        this.onTouch(this.btnLiBao, this.onTouchLiBao, this);
        this.onTouch(this.btnDaoJu, this.onTouchDaoJu, this);
        this.onTouch(this.btnSkin, this.onTouchSkin, this);

        this.onTouch(this.closeBtn,this.onCloseBtnClick,this);
        this.payWayPage.active = false;
        cc.game.on(GameEvent.SHOW_PAYWAY_PAGE, this.SHOW_PAYWAY_PAGE, this);
    }


    onCloseBtnClick(){
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SHOP, false);
    }
    
    SHOW_PAYWAY_PAGE(goodsId: number) {
        GlobalVar.currentBuyGoodsId = goodsId;
        HttpCom.getPayWay(goodsId, (payWays: Payment) => {
            let payWay: PayWay = this.payWayPage.getComponent(PayWay);

            payWay.init(payWays);
            this.payWayPage.active = true;
        })
    }

    show(): void {
        super.show();
        this.payWayPage.active = false;
        this.initPanel();
    }
    initPanel() {
        if (this.panelType == null) {
            this.panelType = PanelType.LiBao
        }
        this.removeItem();
        this.comingSoon.active = false;
        this.star.active = false;
        if (this.panelType == PanelType.LiBao) {
            HttpCom.goodsList("package", (res) => {
                let arr = this.createListContent("package", res.data);
                this.CreateListItem(this.content, this.libaoItem, arr, LiBaoItem)
            })
        } else if (this.panelType == PanelType.DaoJu) {
            HttpCom.goodsList("item", (res) => {
                let arr = this.createListContent("item", res.data);
                this.CreateListItem(this.content, this.daojuItem, arr, DaoJuItem)
            })
            // this.CreateListItem(this.content, this.keyItem, KeyConfig, KeyItem)
        } else if (this.panelType == PanelType.Skin) {
            HttpCom.goodsList("skin")
            this.comingSoon.active = true;
            this.star.active = true;
        }
        this.updateBtn();
    }
    createListContent(name: string, res: Array<any>): Array<any> {
        let arr = [];
        for (let i = 0; i < res.length; i++) {
            if (res[i].tab == name) {
                arr.push(res[i])
            }
        }
        return arr;
    }
    updateBtn() {
        this.btnLiBao.getComponent(cc.Sprite).spriteFrame = (this.panelType == PanelType.LiBao ? SpriteManager.shopBtnIcon[0] : SpriteManager.shopBtnIcon[1]);
        this.btnDaoJu.getComponent(cc.Sprite).spriteFrame = (this.panelType == PanelType.DaoJu ? SpriteManager.shopBtnIcon[0] : SpriteManager.shopBtnIcon[1]);
        this.btnSkin.getComponent(cc.Sprite).spriteFrame = (this.panelType == PanelType.Skin ? SpriteManager.shopBtnIcon[0] : SpriteManager.shopBtnIcon[1]);
    }
    onTouchLiBao() {
        if (this.panelType == PanelType.LiBao) {
            return;
        }
        this.panelType = PanelType.LiBao;
        this.initPanel();
    }
    onTouchDaoJu() {
        if (this.panelType == PanelType.DaoJu) {
            return;
        }
        this.panelType = PanelType.DaoJu;
        this.initPanel();
    }
    onTouchSkin() {
        if (this.panelType == PanelType.Skin) {
            return;
        }
        this.panelType = PanelType.Skin;
        this.initPanel();
    }
    private removeItem() {
        let child = this.content.children;
        for (let i = child.length - 1; i >= 0; i--) {
            PoolManager.instance.putNode(child[i]);
        }
    }
}
