import { ENUM_UI_TYPE } from "../Enum";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import { StaticInstance } from "../StaticInstance";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ToastLayer extends BaseLanguageLayer {
    panel: cc.Node = null;
    btnClose: cc.Node = null;
    btnQueDing: cc.Node = null;
    tipsLabel: cc.Label = null;
    onLoad(): void {
        super.onLoad();
        this.panel = cc.find('style/panel', this.node)
        this.btnClose = cc.find('btn_close', this.panel);
        this.btnQueDing = cc.find('btn_submit', this.panel);
        this.tipsLabel = cc.find('tip', this.panel).getComponent(cc.Label);
        this.onTouch(this.btnClose, this.onClose, this);
        this.onTouch(this.btnQueDing, this.onClose, this);
    }

    show(data: any): void {
        super.show();
        if (data && Languages[data]) {
            this.tipsLabel.string = Languages[data][DataManager.instance.language];
        }
    }
    protected onEnable(): void {
        this.zoomIn(this.panel);
    }

    private onClose() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TOAST, false);
        // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MAIN_LEVEL, false);
    }

}