import { GameEvent, PayType } from "../data/EventName";
import { GlobalVar } from "../data/GlobalVar";
import { ENUM_AUDIO_CLIP } from "../Enum";
import AudioManager from "../manager/AudioManager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PayWayItem extends cc.Component {

    

    payTpye: PayType = PayType.star;

    price: number = 0;
    // LIFE-CYCLE CALLBACKS:

    onLoad () {

        this.node.on(cc.Node.EventType.TOUCH_END,this.onClick,this)
    }

    protected onDestroy(): void {
        this.node.off(cc.Node.EventType.TOUCH_END,this.onClick,this)
    }

    init(payTpye: PayType, price?: number){
        this.payTpye = payTpye;

        if(price){
            this.price = price;
        }
    } 


    onClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK);
        if(GlobalVar.currentBuyGoodsId !== null){
            cc.game.emit(GameEvent.BUY_GOODS,GlobalVar.currentBuyGoodsId,this.payTpye)
        }
    }

    start () {

    }

    // update (dt) {}
}
