import { GlobalVar } from "../data/GlobalVar";
import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE } from "../Enum";
import { Languages } from "../Languages";
import DataManager from "../manager/DataManager";
import EventManager, { EventType } from "../manager/EventManager";
import SpriteManager from "../manager/SpriteManager";
import ToastManager from "../manager/ToastManager";
import { StaticInstance } from "../StaticInstance";
import { formatSeconds } from "../Utils";
import BaseLanguageLayer from "./BaseLanguageLayer";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LevelUILayer1 extends BaseLanguageLayer {
    btnPause: cc.Node = null
    btnLevel: cc.Node = null;
    btnSkills: cc.Node = null;
    btnSuoduan: cc.Node = null;
    btnTouming: cc.Node = null;
    btnYichu: cc.Node = null;

    onLoad() {
        super.onLoad();
        this.btnPause = cc.find('btn_pause', this.node)
        this.btnLevel = cc.find('btn_level', this.node)
        this.btnSkills = cc.find('skills', this.node);
        this.btnSuoduan = cc.find('btn_shuffle', this.btnSkills);
        this.btnTouming = cc.find('btn_opacity', this.btnSkills);
        this.btnYichu = cc.find('btn_delete', this.btnSkills);
        this.onTouch(this.btnPause, this.onPauseClick, this);
        this.onTouch(this.btnLevel, this.onLevelClick, this);
        this.onTouch(this.btnSuoduan, this.onSuoDuanClick, this);
        this.onTouch(this.btnTouming, this.onTouMingClick, this);
        this.onTouch(this.btnYichu, this.onYiChuClick, this);
        EventManager.instance.on(EventType.OPEN_LEVEL_BTN, this.openLevelBtn, this);
        EventManager.instance.on(EventType.CLOSE_LEVEL_BTN, this.closeLevelBtn, this);
    }
    show(): void {
        super.show();

    }

    start() {
        this.btnSkills.active = false;
    }

    private openLevelBtn() {
        this.btnSkills.active = true;
    }
    private closeLevelBtn() {
        this.btnSkills.active = false;
    }

    onPauseClick() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING)
    }
    onLevelClick() {
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEVEL_SELECT)
    }
    onSuoDuanClick() {//缩短
        if (DataManager.instance.isSkilling) {
            return;
        }
        DataManager.instance.skillIndex = 0
        // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SKILL_SUBMIT)
        this.openSkilling();
    }
    onTouMingClick() {//透明
        if (DataManager.instance.isSkilling) {
            return;
        }
        DataManager.instance.skillIndex = 1
        // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SKILL_SUBMIT)
        this.openSkilling();
    }
    onYiChuClick() {//移除
        if (DataManager.instance.isSkilling) {
            return;
        }
        DataManager.instance.skillIndex = 2
        // StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SKILL_SUBMIT)
        this.openSkilling();
    }
    private openSkilling() {
        let keys = 1
        if (DataManager.instance.skillIndex > 1) keys = 2
        if (DataManager.instance.keys < keys) {
            console.log("钥匙不够")
            ToastManager.instance.show(Languages['toast5'][DataManager.instance.language], { gravity: 'TOP', bg_color: cc.color(226, 69, 109, 255) })
        } else {
            console.log(DataManager.instance.skillIndex, keys, "-------------------")
            DataManager.instance.isSkilling = true
            StaticInstance.uiManager.setMainSkillTip()
            StaticInstance.uiManager.setMainTimer(true)
            StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SKILL_SUBMIT, false)
        }
    }

    // update (dt) {}
}
